<script>
import HomePortfolio from '../components/HomePortfolio';
import Footer from '../components/Footer';
import Habitat from '../components/portfolio-items/Habitat';
import Tallowwood from '../components/portfolio-items/Tallowwood';
import Lume from '../components/portfolio-items/Lume';
import ScrollDown from '../components/ScrollDown'
export default {
  name: 'Portfolio',
  components: {
    'app-tallowwood': Tallowwood,
    'app-habitat': Habitat,
    'app-lume': Lume,
    'app-footer': Footer,
    'app-home-portfolio': HomePortfolio,
    'app-scroll-down': ScrollDown
  },
  methods: {
    onPortfolioCta () {
      let appPortfolio = $(this.$refs.appPortfolio.$el);
      $('html,body').animate({
        scrollTop: appPortfolio.offset().top },
      'slow');
    }
  }
};
</script>

<style scoped>
  @media screen and (max-width: 1092px) {
